import { Component } from '@angular/core';
import { NavController, LoadingController, ModalController, Platform, NavParams, ViewController, ToastController } from 'ionic-angular';
import { AuthService } from '../../providers/auth-service/auth-service';
import { LaunchNavigator } from '@ionic-native/launch-navigator';

import { VerificationPage } from '../verification/verification';

@Component({
  selector: 'page-kelembagaan',
  templateUrl: 'kelembagaan.html'
})
export class KelembagaanPage {
  // data: Array<{ noRegistrasi: string, nama: string, namaBentukLembaga: string, urlGambarLogo: string, statusVerifikasi: string }>;
  data: any;
  pages: Array<{ component: any }>;
  loading: any;
  page = 1;
  
  constructor(public navCtrl: NavController, public authService: AuthService, public loadingCtrl: LoadingController, public modalCtrl: ModalController, private toastCtrl: ToastController) {
    // this.navCtrl.setRoot(TabsPage, {})
    this.getData();
    // console.log("====");
    // console.log(this.data);
    // this.data = [
    //   { noRegistrasi: '1', nama: 'Lembaga 1', namaBentukLembaga: 'Yayasan', urlGambarLogo: 'avatar-1.jpg', statusVerifikasi: '1' },
    // ];

    // set our app's pages
    this.pages = [
      { component: KelembagaanFilterPage },
      { component: KelembagaanDetailPage },
    ];
  }

  openModal(index) {
    let modal = this.modalCtrl.create(this.pages[index].component);
    modal.present();
  }

  getData() {
    this.showLoader();
    this.authService.getListLembaga(this.page).then((result) => {
      this.loading.dismiss();
      this.data = result;
      this.page = this.page + 1;
    }, (err) => {
      this.loading.dismiss();
      // this.presentToast(err);
      return false;
    });
  }

  loadData(infiniteScroll) {
    this.authService.getListLembaga(this.page).then((result) => {
      let temp = [result];
      for (var i = 0; i < temp.length; i++) {
        this.data.push(result[i]);
      }

      infiniteScroll.complete();
      this.page = this.page + 1;

      // if (this.data.length > 90) {
      //   infiniteScroll.enable(false);
      // }
    });
  }

  showLoader() {
    this.loading = this.loadingCtrl.create({
      content: 'Memuat data...'
    });

    this.loading.present();
  }

  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom',
      dismissOnPageChange: true
    });

    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });

    toast.present();
  }

}


