import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { ToastController } from 'ionic-angular';


@Injectable()
export class AuthService {

  constructor(public http: Http, private toastCtrl: ToastController) { }

  login(credentials) {
    return new Promise((resolve, reject) => {
      let headers = new Headers();
      headers.append('Content-Type', 'application/json');

      this.http.post('http://api.syncardtech.com/dplega-api/public/auth/', JSON.stringify(credentials), { headers: headers })
        .subscribe(res => {
          console.log(res);
          resolve(res.json());
        }, (err) => {
          reject(err);
        });
    });
  }

  logout() {
    return new Promise((resolve, reject) => {
      // let headers = new Headers();
      // headers.append('Content-Type', 'application/json');
      // headers.append('X-Auth-Token', localStorage.getItem('token'));

      this.http.get('http://api.syncardtech.com/dplega-api/public/books/', {})
        .subscribe(res => {
          console.log(res);
          localStorage.clear();
          resolve(res.json());
        }, (err) => {
          reject(err);
        });
    });
  }

  getListLembaga(page) {
    return new Promise((resolve, reject) => {
      // this.presentToast('http://api.syncardtech.com/dplega-api/public/list/lembaga/' + page);
      this.http.get('http://api.syncardtech.com/dplega-api/public/list/lembaga/' + page, {})
      .subscribe(res => {
        console.log(res);
        this.presentToast(res);
        resolve(res.json());
      }, (err) => {
        this.presentToast("hasilnuya :" + err);
          reject(err);
        });
    });
  }

  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 5000,
      position: 'bottom',
      dismissOnPageChange: false
    });

    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });

    toast.present();
  }

}


